/* .condiv class is for the right side content container*/
.condiv {
  padding: 100px;
  background-color: rgb(230, 230, 230);
  flex: var(--condiv-flex);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

nav {
  padding: 60px 0px 0px 0px;
  flex: var(--sidebar-flex);
  /*
  background: rgb(238, 103, 143);
  background: linear-gradient(
    180deg,
    rgba(150, 150, 150, 1) 50%,
    rgba(0, 0, 888888880, 1) 80%
  );
  */
  background-image: url(/static/media/sunset_tree.91a538dc.jpeg);
  background-size: auto 100%;
}

nav ul {
  font-size: var(--fontsize-nav);
  font-style: bold;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
}

nav ul li {
  padding: 10px 90px 10px 90px;
  margin-top: 25px;
  margin-bottom: 25px;
}

a {
  text-decoration: none;
  color: inherit;
}
