*/

li.active a {
  text-decoration: none;
  color: white;
  font-weight: bolder;
}

.portrait {
  border-radius: 50%;
  width: 300px;
  height: 300px;
  margin: 30px;
}

.portrait:hover {
  width: 310px;
  height: 310px;
  margin: 25px 30px 25px 20px;
}

.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
  padding: 10px;
}
.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.skills ul li {
  list-style-type: none;
  margin: 10px;
  padding: 5px;
  align-items: center;
}

.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.center {
  display: flex;
  align-items: left;
  text-align: center;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.rating {
  float: right;
  align-items: center;
  margin: 0px;
}

.skills-left {
  float: left;
}

.skills-right {
  float: left;
}


